.xs-to-sm {
  display: none;
}
@media (max-width: 576px), (min-width: 576px) and (max-width: 768px) {
  .xs-to-sm {
    display: initial;
  }
}
.responsive-breakable {
  word-wrap: break-word;
  width: 10px;
}
@media (max-width: 576px), (min-width: 576px) and (max-width: 768px) {
  .superfluous-text {
    display: none;
  }
}
#header-search input,
#header-search button {
  border-radius: 0;
}
#header-search #search-input {
  width: 35vw;
}
@media (max-width: 576px), (min-width: 576px) and (max-width: 768px) {
  #header-search #search-input {
    width: 20vw;
  }
}
@media (max-width: 576px), (min-width: 576px) and (max-width: 768px) {
  .card-columns {
    -webkit-column-count: 1;
            column-count: 1;
  }
}
@media (min-width: 576px) and (max-width: 768px), (min-width: 768px) and (max-width: 992px) {
  .card-columns {
    -webkit-column-count: 2;
            column-count: 2;
  }
}
.card {
  border-width: 0;
}
.card-button {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
@media (max-width: 576px) {
  .resizable-text {
    font-size: 0.2em;
  }
}



